import * as React from 'react';
import Button from '@mui/material/Button';
import DialogTitle from '@mui/material/DialogTitle';
import Dialog from '@mui/material/Dialog';
import {Alert, Grid} from "@mui/material";
import {useState} from "react";


// @ts-ignore
function PhotoDialog({imageUrl, answer, stateMessage, linkToShare}) {
    const [resultCopied, setResultCopied] = useState(false)
    const handleShareClicked = async () => {
        const textToCopy = `${stateMessage}, Try it too! ${String.fromCodePoint(0x1F643)
        } \n ${linkToShare}`
        await navigator.clipboard.writeText(textToCopy)
        setResultCopied(true)
    }
    const renderResultCopied = () => {
        return (
            <Alert severity={'info'}>{"Result copied to clipboard"}</Alert>
        )
    }

    return (
        <Dialog open={true} fullWidth={true} maxWidth={"xl"} >
            <Grid container direction={'column'} alignItems={'center'} p={3}>
                <Grid item>
                    <DialogTitle>{answer}</DialogTitle>
                </Grid>

                <Grid item>
                    <img src={imageUrl} alt={answer}/>
                </Grid>
                <Grid item>
                    {resultCopied && renderResultCopied()}
                </Grid>
                <Button onClick={handleShareClicked}>
                    Share Results!
                </Button>
            </Grid>
        </Dialog>
    );
}

export default PhotoDialog