import axios from "axios";
import ArrowForwardIcon from '@mui/icons-material/ArrowForward';
import {Alert, Button, Grid, Input, Paper} from "@mui/material";
import Confetti from 'react-confetti'
import React, {useEffect, useState} from 'react';
import DailyRiddleHeader from "../components/DailyRiddleHeader";
import RiddleQuestion from "../components/RiddleQuestion";
import AttemptFactory from "../components/AttemptFactory";
import Answer from "../components/Answer";
import PhotoDialog from "../components/PhotoDialog";

const client = axios.create({
    // baseURL: "http://localhost:3015"
});

function DailyRiddle() {
    const INITIAL = 'initial'
    const SUCCESS = 'success'
    const FAILURE = 'failure'
    const threeAttempts = [{answer: '', isCorrect: false}, {answer: '', isCorrect: false}, {
        answer: '',
        isCorrect: false
    }, {answer: '', isCorrect: false}, {answer: '', isCorrect: false}]
    const [riddle, setRiddle] = useState({question: '', answer: ''})
    const [userAnswer, setUserAnswer] = useState('')
    const [linkToShare, setLinkToShare] = useState('')
    const [riddlePhotoUrl, setRiddlePhotoUrl] = useState('')
    const [showConfetti, setShowConfetti] = useState(false)
    const [stateMessage, setStateMessage] = useState('')
    const [state, setState] = useState(INITIAL)
    const [attempts, setAttempts] = useState(threeAttempts)

    const [showAnswer, setShowAnswer] = useState(true)

    useEffect(() => {
        client.get('/daily_riddle').then((response) => {
            setRiddle(response.data.payload.dailyRiddle);
            setLinkToShare(response.data.payload.miscData.linkToShare)
            setRiddlePhotoUrl(response.data.payload.miscData.riddlePhoto)
        });
    }, []);
    useEffect(() => {
    }, [attempts])

    const handleAnswerChange = (event: { target: { value: string; } }) => {
        setUserAnswer(event.target.value)
    }

    function handleCorrectAnswer() {
        setStateMessage('Nailed It!!')
        setState(SUCCESS)
        setShowConfetti(true)
    }

    function handleBadAnswer() {
        setStateMessage("Couldn't get it, maybe Next time")
        setState(FAILURE)
    }

    const handleAnswerSubmitted = () => {
        let normalizeUserAnswer = userAnswer.trim().toLowerCase()
        let normalizeAnswer = riddle.answer.trim().toLowerCase()
        const isCorrect = normalizeAnswer == normalizeUserAnswer
        updateAttempts({answer: userAnswer, isCorrect})
        if (isCorrect) {
            handleCorrectAnswer();
            setShowAnswer(false)
        }
        if (getFirstEmptyAttemptIndex() === attempts.length - 1 && !isCorrect) {
            handleBadAnswer();
            setShowAnswer(false)
        }
        setUserAnswer("")
    }


    function getFirstEmptyAttemptIndex() {
        return attempts.findIndex(a => a.answer === '');
    }

    function updateAttempts(attempt: { answer: string; isCorrect: boolean }) {
        let firstEmptyAttemptIndex = getFirstEmptyAttemptIndex()
        if (firstEmptyAttemptIndex !== -1) {
            let tempAttempts = [...attempts]
            tempAttempts.splice(firstEmptyAttemptIndex, 1, attempt)
            setAttempts(tempAttempts)
        }
    }

    const renderSuccess = () => {
        return (
            <Grid item>
                <Alert severity={'success'}>{stateMessage}</Alert>
            </Grid>)
    }
    const renderFailure = () => {
        return (
            <Grid item>
                <Alert severity={'error'}>{stateMessage}</Alert>
            </Grid>)
    }


    const onSubmitForm = (e: any) => {
        e.preventDefault();
    }

    return (
        <Grid container direction={'column'} alignContent={'center'} justifyContent={'center'} p={5}>
            <Grid item>
                {showConfetti &&
                    <Confetti recycle={false}
                    />
                }
            </Grid>
            <Grid item>
                <DailyRiddleHeader/>
            </Grid>
            <Grid item>
                <Grid container justifyContent={"center"} md={12}>
                    <Grid item md={9}>
                        <RiddleQuestion riddle={riddle}/>
                    </Grid>
                </Grid>
            </Grid>
                {state === SUCCESS && renderSuccess()}
                {state === FAILURE && renderFailure()}
            <Grid item>
                {showAnswer &&
                    <Grid container justifyContent={'center'}>
                        <Grid item>
                            <Paper component={"form"} onSubmit={onSubmitForm}
                                   sx={{
                                       display: "flex",
                                       flexWrap: "wrap",
                                       alignItems: 'center',
                                       m: 2,
                                       width: 260,
                                       height: 40,

                                   }}>
                                <Input
                                    sx={{ml: 1, flex: 1}}
                                    value={userAnswer}
                                    onKeyDown={(e) => {
                                        if (e.key === 'Enter') {
                                            handleAnswerSubmitted()
                                        }
                                    }}
                                    onChange={handleAnswerChange}
                                    placeholder="Answer goes here"
                                    inputProps={{'aria-label': 'search google maps'}}
                                />
                                <Button onClick={handleAnswerSubmitted}>
                                    <ArrowForwardIcon fontSize={'large'}/>
                                </Button>
                            </Paper>
                        </Grid>
                    </Grid>
                }

            </Grid>
            <Grid item>
                <Grid container direction={'column'} alignContent={'center'}>
                    {attempts.map((attempt, index) => {
                        return (<AttemptFactory attempt={attempt} key={index.toString() + attempt.answer}/>)
                    })
                    }
                </Grid>
                <Grid container direction={'column'} alignContent={'center'}>

                    {state !== INITIAL && <PhotoDialog answer={riddle.answer} imageUrl={riddlePhotoUrl}
                                                       stateMessage={stateMessage} linkToShare={linkToShare}/>}

                </Grid>
            </Grid>
        </Grid>)
}

export default DailyRiddle;
